import { BrowserRouter, HashRouter, Link, Route, Routes } from 'react-router-dom';
import { useContext, useEffect } from 'react';
import ReactAudioPlayer from 'react-audio-player';
import KakaoLogin from "react-kakao-login";

import icon_note from '../img/icon_music.svg'; //음표아이콘
import icon_search from '../img/icon_search.svg'; //돋보기아이콘

import rainSound from '../sound/rainSound.mp3';
import waveSound from '../sound/waveSound.mp3';
import seagullSound from '../sound/seagullSound.mp3';
import firewoodSound from '../sound/firewoodSound.mp3';
import smallRiverSound from '../sound/smallRiverSound.mp3';
import birdSound from '../sound/birdSound.mp3';


import rainSoundImg from '../img/whiteNoise_rain.jpg';
import waveSoundImg from '../img/whiteNoise_wave.jpg';
import seagullSoundImg from '../img/whiteNoise_seagull.jpg';
import firewoodSoundImg from '../img/whiteNoise_firewood.jpg';
import smallRivalSoundImg from '../img/whiteNoise_smallRiver.jpg';
import birdSoundImg from '../img/whiteNoise_bird.jpg';

import { MyContext } from './Context';

export default function FixedCompo() {
  
    let {id1List, setId1List,   loginObj, setLoginObj,   loginBool, setLoginBool} = useContext(MyContext);

  console.log('==============', loginObj, loginObj?.properties.nickname);
    useEffect(()=>{
        const elMenuBtn = document.querySelector('.headerContents button.menuBtn'),//버거메뉴
              elVerticalMenu = document.querySelector('.verticalMenu'),//수직 메뉴창
              elmenuXBtn = document.querySelector('.verticalMenu button.menuXBtn');//메뉴창 x버튼
    
        const elSoundBtn = document.querySelector('footer .whiteNoise'),//푸터 음악 버튼
              elSoundImg = document.querySelector('footer .whiteNoise img'),//푸터 음악 버튼의 이미지
              elWhiteNoiseUi = document.querySelector('footer .whiteNoiseUi'),//백색소음 ui
              elSoundBtns = elVerticalMenu.querySelectorAll('ul.soundBtnWrapper > li.soundBtn');//메뉴창 백색소음 버튼들
        
        
        //버거메뉴 클릭시, 메뉴창 나오기
        elMenuBtn.onclick = () => {
          elVerticalMenu.classList.add('active');
        }
        //x버튼 클릭시, 메뉴창 닫기
        elmenuXBtn.onclick = () => {
          elVerticalMenu.classList.remove('active');
        }
    
        //푸터의 사운드 버튼 클릭시, 사운드 ui나오기
        elSoundBtn.onclick = () => {
          elWhiteNoiseUi.classList.toggle('active');
        }
    
        let preEle = elSoundBtns[5]; //이전 사운드버튼 새소리버튼(elSoundBtns[5])으로 초기화
        elSoundBtns.forEach((ele, k) => {
          ele.onclick = (e) => {
            let soundLink = '';
            let imgLink = '';
            let err = false;
            switch(k){
              case 0:
                soundLink = rainSound;
                imgLink = rainSoundImg;
                break;
              case 1:
                soundLink = waveSound;
                imgLink = waveSoundImg;
                break;
              case 2:
                soundLink = seagullSound;
                imgLink = seagullSoundImg;
                break;
              case 3:
                soundLink = firewoodSound;
                imgLink = firewoodSoundImg;
                break;
              case 4:
                soundLink = smallRiverSound;
                imgLink = smallRivalSoundImg;
                break;
              case 5:
                soundLink = birdSound;
                imgLink = birdSoundImg;
                break;
              default: 
                console.log('사운드 오류가 났습니다.');
                err = true;
            }
    
            if(!err){
              elWhiteNoiseUi.setAttribute('src', soundLink);
              elSoundImg.setAttribute('src', imgLink);
            }
    
            preEle.classList.remove('active');
            e.target.classList.add('active');
            preEle = e.target;
          }
        });
    
    
    
      },[]);
    
      
      const kakaoLogin = () => {
        Kakao.init('be02ad515516537263f1362bbaeb2e5b'); //발급받은 키 중 javascript키를 사용해준다.
        console.log(Kakao.isInitialized()); // sdk초기화여부판단


        Kakao.Auth.login({
          success: function (response) {
            Kakao.API.request({
              url: '/v2/user/me',
              success: function (response) {
                  console.log('로그인',response);
                  setLoginObj(response);
                  setLoginBool(true);
              },
              fail: function (error) {
                console.log(error);
              },
            })
          },
          fail: function (error) {
            console.log(error);
          },
        })
      }//kakaoLogin() 함수정의
  
      const kakaoLogout = () => {

        if (Kakao.Auth.getAccessToken()) {
          Kakao.API.request({
            url: '/v1/user/unlink',
            success: function (response) {
                console.log('로그아웃',response);
                setLoginObj('');
                setLoginBool(false);
            },
            fail: function (error) {
              console.log(error);
            },
          })
          Kakao.Auth.setAccessToken(undefined);
        }
      }//kakaoLogout 함수정의

  return (
    <>
      <header>
        <div className='headerContents'>
          <h1 className='logo'><Link to="/">책과 리뷰한잔</Link></h1>
          <button type='button' className='menuBtn'></button>
        </div>
      </header>

      <footer>
        <ReactAudioPlayer
          className='whiteNoiseUi'
          src={seagullSound}
          autoPlay
          controls
        />

        <Link to="/Search" className='search'>
          <img src={icon_search} alt='돋보기 아이콘_검색장소로 이동'/>
        </Link>
        <div className='nickname' onClick={kakaoLogout}>{loginBool ? loginObj?.properties.nickname : '로그아웃'}</div>
        <div className='whiteNoise' title='클릭하면 사운드UI가 나옵니다.'>
          <div className='bg'>
            <img src={birdSoundImg} alt='백색소음 이미지'/>
          </div>
          <img src={icon_note} alt='음표 아이콘'/>
        </div>
      </footer>





      <div className='verticalMenu'>
        {/* x버튼 */}
        <button type='button' className='menuXBtn'></button>

        {/* 컨텐츠 */}
        <div className='content'>

          <div className='nicknameWrapper'>
            <span className='nickname'>{loginBool ? loginObj?.properties.nickname : '닉네임 님'}</span>
            <button className='nicknameModify'>수정</button>
          </div>
          <ul className='soundBtnWrapper'>

            <li className='soundBtn'>
              <div className='soundMark'>
                <img src={icon_note} alt='음표'/>
              </div>
            </li>

            <li className='soundBtn'>
              <div className='soundMark'>
                <img src={icon_note} alt='음표'/>
              </div>
            </li>

            <li className='soundBtn'>
              <div className='soundMark'>
                <img src={icon_note} alt='음표'/>
              </div>
            </li>

            <li className='soundBtn'>
              <div className='soundMark'>
                <img src={icon_note} alt='음표'/>
              </div>
            </li>
            
            <li className='soundBtn'>
              <div className='soundMark'>
                <img src={icon_note} alt='음표'/>
              </div>
            </li>

            <li className='soundBtn active'>
              <div className='soundMark'>
                <img src={icon_note} alt='음표'/>
              </div>
            </li>

          </ul>

          <div className='MyBookListWrapper'>
            <Link className='moveToList' to="/MyList">
              My Book List
            </Link>

            <ul className='MyBookList'>
              {
                id1List.slice(0,3).map((obj, k)=>(
                    <li key={k}><span>{obj.title}</span></li>
                ))
              }
            </ul>
          </div>
  
        </div>

        <div className='loginWrapper'>

          {/* <div className='loginGoogle'>
            <div className='left'>
              <p></p>
            </div>
            구글로 로그인
          </div> */}

          <div className='loginKakao' onClick={kakaoLogin}>
            <div className='left'>
              <p></p>
            </div>
             카카오로 로그인
          </div>

        </div>{/* loginWrapper */}
      </div>
    </>
  )
}
